<script>

	import viewModel from  '@/components/sqoop/index/viewModel.vue'

	export default {
		data () {
			return {
				instance: false,
				columns1: [
					{
						title: '姓名',
						key: 'name'
					},
					{
						title: '年龄',
						key: 'age'
					},
					{
						title: '地址',
						key: 'address'
					}
				],
				data1: [
					{
						name: '王小明',
						age: 18,
						address: '北京市朝阳区芍药居'
					},
					{
						name: '张小刚',
						age: 25,
						address: '北京市海淀区西二旗'
					},
					{
						name: '李小红',
						age: 30,
						address: '上海市浦东新区世纪大道'
					},
					{
						name: '周小伟',
						age: 26,
						address: '深圳市南山区深南大道'
					}
				]
			}
		},
		components:{
			viewModel
		},
		methods: {
			open () {
				this.instance = true
			},

			ok () {
				this.$Message.info('点击了确定');
			},
			cancel () {
				this.$Message.info('点击了取消');
			}
		}
	}
</script>

