<template>
    <div style="margin:30px">
        <Row>
            <i-col span="16">
                <h2 style="color: #464c5b;font-size: 23px">自助导入列表</h2>
            </i-col>
            <i-col span="6">
                <span style="float: right">
                    <Input v-model="input_model" placeholder="请输入..." style="width: 200px;margin-right: 5px"> </Input>
                    <!--<Button type="ghost" shape="circle" icon="ios-search">搜索</Button>-->
                      <Button type="ghost" shape="circle" icon="ios-search"></Button>

                </span>
            </i-col>
            <i-col span="2">
                <Button type="primary" style="background-color: #0593d3;border-color: #0593d3;
                                    float: right;font-weight: 500;font-size: 14px">导入
                </Button>
            </i-col>
        </Row>


        <Row>
            <i-col span="24">
                <Table :context="self" :data="tableData1" :columns="tableColumns1" :row-class-name="rowClassName"
                       stripe></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="100" :current="1" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>


        <view-dialog  ref="viewModel"></view-dialog>
    </div>
</template>
<script>
    import viewDialog from  '@/components/sqoop/index/viewDialog.vue'
	export default {
		data () {
			return {
				input_model: "",
				self: this,
				tableData1: this.mockTableData1(),
				tableColumns1: []
			}
		},
		components:{
			viewDialog
        },
		methods: {
			initTableFormat(){
				this.tableColumns1 = [
					{
						title: "唯一标示",
						key: "uuid"
					},
					{
						title: '名称',
						key: 'name'
					},
					{
						title: '类型',
						key: 'status'
					},
					{
						title: '数据描述',
						width: '30%',
						key: 'portrayal'
					},
					{
						title: '创建时间',
						key: 'time',
						render (row, column, index) {
							return `{{ formatDate(tableData1[${index}].update) }}`;
						}
					},
					{
						title: '更新时间    ',
						key: 'update',
						render (row, column, index) {
							return `{{ formatDate(tableData1[${index}].update) }}`;
						}
					},
					{
						title: '记录总数',
						key: 'people'
					},
					{
						title: '操作',
//						key: 'people',
						render (row, column, index) {
							return `
                                 <Dropdown trigger="click">
                                        <a href="javascript:void(0)">
                                            数据操作
                                            <Icon type="arrow-down-b"></Icon>
                                        </a>
                                        <Dropdown-menu slot="list">
                                            <Dropdown-item  @click.stop="">
                                              <a @click.stop="rowClickAction('${row.uuid}','view')">数据预览</a>
                                            </Dropdown-item>
                                            <Dropdown-item>
                                                <a @click.stop="rowClickAction('${row.uuid}','add')">数据追加</a>
                                            </Dropdown-item>
                                            <Dropdown-item>
                                             <a @click.stop="rowClickAction('${row.uuid}','mange')"> 数据管理</a>
                                            </Dropdown-item>
                                            <Dropdown-item disabled>编辑｛未实现｝</Dropdown-item>
                                            <Dropdown-item divided>
                                              <a @click.stop="rowClickAction('${row.uuid}','delete')">删  除</a>
                                              </Dropdown-item>
                                        </Dropdown-menu>
                                    </Dropdown>
                                `;
						}
					}
				]
			},
			mockTableData1 (index) {
				index == "" || index == undefined ? index = 1 : index = index;
				let data = [];
				for (let i = 0; i < 10; i++) {
					data.push({
						uuid: index + "_" + i,
						name: '商圈' + Math.floor(Math.random() * 100 + 1),
						status: Math.floor(Math.random() * 3 + 1),
						portrayal: '随机导入数据－－描述',
						time: new Date(),
						update: new Date(),

						people: Math.floor(Math.random() * 1000 + 1)
					})
				}
				return data;
			},
			formatDate (date) {
				const y = date.getFullYear();
				let m = date.getMonth() + 1;
				m = m < 10 ? '0' + m : m;
				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d;
				return y + '-' + m + '-' + d;
			},
			changePage (index) {
				console.log("当前页数:" + index);
				// 这里直接更改了模拟的数据，真实使用场景应该从服务端获取数据
				this.tableData1 = this.mockTableData1(index);
			},


			rowClickAction(row,type){
				console.log(row);
				this.openViewDialog();

//				event.preventDefault();
//				event.stopPropagation();

//				alert(JSON.stringify(row));
			},


			openViewDialog() {
				this.$refs.viewModel.open();
			},


			rowClassName (row, index) {
				return 'demo-table-row';
			}
		},

		created () {
			this.initTableFormat()

		}
	}
</script>
<style>
    .ivu-table {
        font-size: 14px;
    }

    ul, ol {
        padding-left: 0px;
    }

    .ivu-table .demo-table-row td {
        /*background-color: #2db7f5;*/
        height: 48px;
    }
</style>



