<template>
    <div>
        <Table  width="1020"  :data="tableData1" :columns="tableColumns1" stripe  :row-class-name="rowClassName"></Table>
        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="100" :current="1" @on-change="changePage"></Page>
            </div>
        </div>
    </div>
</template>
<script>
	export default {
		data () {
			return {
				self: this,
				tableData1: this.mockTableData1(),
				tableColumns1: [
					{
						title: '名称',
						width: 250,
						key: 'name'
					},
					{
						title: '状态',

						width: this.rowLenth,
						key: 'status'
					},
					{
						title: '画像内容',
						width: this.rowLenth,
						key: 'status'
					},
					{
						title: '选定人群数',
						width: this.rowLenth,
						key: 'status'
					}
				]
			}
		},
		methods: {
			mockTableData1 () {
				let data = [];
				for (let i = 0; i < 10; i++) {
					data.push({
						name: '商圈' + Math.floor(Math.random () * 100 + 1),
						status: Math.floor(Math.random () * 3 + 1),
						portrayal: ['城市渗透', '人群迁移', '消费指数', '生活指数', '娱乐指数'],
						people: [
							{
								n: '客群' + Math.floor(Math.random () * 100 + 1),
								c: Math.floor(Math.random () * 1000000 + 100000)
							},
							{
								n: '客群' + Math.floor(Math.random () * 100 + 1),
								c: Math.floor(Math.random () * 1000000 + 100000)
							},
							{
								n: '客群' + Math.floor(Math.random () * 100 + 1),
								c: Math.floor(Math.random () * 1000000 + 100000)
							}
						],
						time: Math.floor(Math.random () * 7 + 1),
						update: new Date()
					})
				}
				return data;
			},
			formatDate (date) {
				const y = date.getFullYear();
				let m = date.getMonth() + 1;
				m = m < 10 ? '0' + m : m;
				let d = date.getDate();
				d = d < 10 ? ('0' + d) : d;
				return y + '-' + m + '-' + d;
			},
			changePage () {
				// 这里直接更改了模拟的数据，真实使用场景应该从服务端获取数据
				this.tableData1 = this.mockTableData1();
			},
			mounted () {
				this.rowLenth = "240px"

			},
			rowClassName (row, index) {
				return 'demo-table-view-row';
			}
		}
	}
</script>

<style>

    .ivu-table .demo-table-view-row td {
        /*background-color: #2db7f5;*/
        height: 40px;
    }

    .ivu-modal-body {
        margin-bottom: -10px;
        padding-bottom: 0;
    }
</style>



